import React, { Component } from 'react';
import {blockquote} from './layout.module.css';

class Catchfrase extends Component {

    constructor(props){ 
        super(props) 
            
        // Set initial state 
        this.state = {quote : 
              'Github development quote'} 
            
        
      } 
    componentDidMount() {

        let myHeaders = new Headers();
        myHeaders.append("Cookie", "_octo=GH1.1.108854488.1628434716; logged_in=no");

        let requestOptions = {
        method: 'GET',
        headers: myHeaders,
        redirect: 'follow'
        };

        fetch("https://api.github.com/zen", requestOptions)
        .then(response => response.text())
        .then(result => this.setState({quote : result}))
        .catch(error => console.log('error', error));
    }
    render() {
      
      return (
        <div className={blockquote} >{this.state.quote}</div>
      )
    }
  }
  export default Catchfrase